// ヘッダーの大きさを、 Milligram のデフォルトから 1 段階ずつ小さくする

h1 {
  font-size: 3.6rem;
  line-height: 1.25;
}

h2 {
  font-size: 2.8rem;
  line-height: 1.3;
}

h3 {
  font-size: 2.2rem;
  letter-spacing: -0.08rem;
  line-height: 1.35;
}

h4 {
  font-size: 1.8rem;
  letter-spacing: -0.05rem;
  line-height: 1.5;
}

h5 {
  font-size: 1.6rem;
  letter-spacing: 0;
  line-height: 1.4;
}
